import { ref, reactive, computed, watch, onMounted } from 'vue'
import { defineStore } from 'pinia'

import API from '@util/API'

export const useLeads = defineStore('leads', () => {


    const isLocked = !formychat_admin_vars.is_premium

    const state = reactive({
        modes: {
            formychat: 'FormyChat',
            cf7: 'Contact Form 7',
        },
        isLoading : true,
        isReloading : false,
        total_formychat_leads : formychat_admin_vars.total.formychat_leads || 0,
        total_cf7_leads : formychat_admin_vars.total.cf7_leads || 0,
        allSelected: false,
    })

    const contents = ref({
        widgets: [],
        cf7_forms: []
    })

    // Fetch contents
    const fetchContents = async () => {

        let data = (await API.get('contents')).data

        // Widgets 
        contents.value.widgets = data.widgets.map( widget => {
            return {
                value: widget.id,
                label: widget.name,
                name: widget.name,
            }
        } )

        // Add all widget to first.
        contents.value.widgets.unshift({
            value: 'all',
            label: 'All Widgets',
            name: 'All Widgets'
        })

        // Set first widget as default if not set.
        if ( !filters.value.widget_id ) {
            filters.value.widget_id = 'all'
        }

        // CF7 Forms
        contents.value.cf7_forms = data.cf7_forms || []


        // Set first form as default if not set.
        if ( !filters.value.cf7_id ) {
            filters.value.cf7_id = contents.value.cf7_forms[0]?.value || null
        }
    }

    // Get Widget Name.
    const getWidgetName = (id) => {
        return contents.value.widgets.find( widget => widget.value == id )?.name || 'Unknown'
    }

    const getCF7FormName = (id) => {
        return contents.value.cf7_forms.find( form => form.value == id )?.name || 'Unknown'
    }
        
    const defaultFilters = () => {
        return {
            mode : 'formychat',
            order_by: 'created_at',
            widget_id : 'all',
            cf7_id : null,
            order: 'desc',
            search: '',
            page: 1,
            per_page: 20,
            after: '',
            before: ''
        }
    }
    
    const filters = ref( defaultFilters() )

    // Leads.
    const leads = ref([])

    const total = computed(() => filters.value.mode === 'formychat' ? state.total_formychat_leads : state.total_cf7_leads)
    const showResetFilter = computed(() => filters.value.search || filters.value.after || filters.value.before) 

    const resetFilter = () => {
        // Replace default filters by key.
        Object.keys(defaultFilters()).forEach(key => {
            filters.value[key] = defaultFilters()[key]
        })
        
    }

    const setSort = (order_by) => {
        if ( filters.value.order_by === order_by ) {
            filters.value.order = filters.value.order === 'asc' ? 'desc' : 'asc'
        } else {
            filters.value.order_by = order_by
            filters.value.order = 'asc'
        }
    }

    // Fetch leads from API.
    const fetchLeads = async () => {
        // Make loading.
        state.isReloading = true

        let query = ''
        Object.keys(filters.value).forEach(key => {
            if ( filters.value[key] !== '' ) {
                query += `${key}=${filters.value[key]}&`
            }
        })
        
        return new Promise( resolve => {
            API.get(`/leads?${query}`).then((response) => {
                leads.value = response.data?.map( (lead, index ) => {
                    lead.locked = isLocked && (index >= 20 || filters.value.page > 1)
                    return lead
                } )
                // console.log('Leads', leads.value);
                resolve( leads.value )
            }).catch((error) => {
                // console.log(error);
            }).finally(() => {
                state.isLoading = false
                state.isReloading = false
            })
        })
    }

    // Watch filters.
    watch(filters.value, fetchLeads)
    // Watch filters.per_page
    watch(() => filters.value.per_page, () => {
        filters.value.page = 1
        fetchLeads()
    })

    onMounted( async ()=> {
        fetchContents()
        await fetchLeads()
        state.isLoading = false
    })
    /**
     * Delete
     */
    const deleteLeads = async (ids) => {

        // If not array, convert to array.
        if ( !Array.isArray(ids) ) {
            ids = [ids]
        }

        return new Promise( resolve => {
            const _leads = leads.value.filter( lead => ids.includes(lead.id) )

            // Set .deleting = true
            _leads.forEach( lead => lead.deleting = true )

            API.delete(`/leads?mode=${filters.value.mode}&id[]=${ids.join('&id[]=')}`, { ids }
            ).then((response) => {
                // console.log('Deleted', response.data);

                if ( response.data.success ) {
                    _leads.forEach( lead => lead.deleted_at = true )

                    // Remove deleted leads after 300 ms.
                    setTimeout(() => {
                        // Reduce total leads.
                        if ( filters.value.mode === 'formychat' ) {
                            state.total_formychat_leads -= ids.length
                        } else {
                            state.total_cf7_leads -= ids.length
                        }

                        // Reset allSelected.
                        state.allSelected = false

                        // Fetch leads again.                
                        fetchLeads()
                    }, 300)

                    resolve(true)
                } else {
                    _leads.forEach( lead => lead.deleting = false )
                    resolve(false)
                }
            })
        })
    }

    // Selectors 

    const toggleAll = () => {
       leads.value.forEach((item, index) => {
            if ( !item.locked ) {
                leads.value[index].selected = state.allSelected;
            }
       })
    };

    const toggleSelector = (lead) => {

        // Find and toggle selected.
        leads.value.find(item => item.id === lead.id).selected = !lead.selected;

        state.allSelected = leads.value.every(item => item.selected);
    };

    const selected = computed(() => {
        return leads.value.filter(item => item.selected) || [];
    });

    // Columns.
    const columns = computed( () => {
        
        if ( filters.value.mode == 'cf7' && leads.value && leads.value.length ) {
            let cols = Object.keys( leads.value[0]?.field )
            
            // return first 4 if more than 4 columns. 
            return cols.length > 4 ? cols.slice(0, 4) : cols
        }

        return ['name', 'email', 'phone', 'message', 'widget_id']
    })

    return {
        state,
        filters,
        total,
        leads,
        showResetFilter,
        resetFilter,
        setSort,
        fetchLeads,
        deleteLeads,
        toggleAll,
        toggleSelector,
        selected,
        contents,
        columns,
        getWidgetName,
        getCF7FormName
    }
})