<script setup>
import { computed, ref, onMounted } from 'vue';
import { useWidgets } from '@store/widgets';
import { Editable, Select, Button, Spin } from '@components';

const $widget = useWidgets();

const whatsapp = computed(() => $widget.currentWidget?.config?.whatsapp || {})
const icon = computed(() => $widget.currentWidget?.config?.icon || {})
const cta = computed(() => $widget.currentWidget?.config?.cta || {})
const form = computed(() => $widget.currentWidget?.config?.form || {})
const cf7 = computed(() => $widget.currentWidget?.config?.cf7 || {})
const greetings = computed(() => $widget.currentWidget?.config?.greetings || {})
const simple = computed(() => $widget.currentWidget.config?.greetings?.templates?.simple || {})
const wave = computed(() => $widget.currentWidget.config?.greetings?.templates?.wave || {})

const device = ref($widget.isMobile ? 'phone' : 'desktop');

const iconPosition = computed(() => {
    if (icon.position !== 'custom') return {};

    return {
        top: icon.position_custom?.top ? icon.position_custom?.top + 'px !important' : 'auto',
        bottom: icon.position_custom?.bottom ? icon.position_custom?.bottom + 'px !important' : 'auto',
        left: icon.position_custom?.left ? icon.position_custom?.left + 'px !important' : 'auto',
        right: icon.position_custom?.right ? icon.position_custom?.right + 'px !important' : 'auto',
    };
})

const headingSizes = { small: '16px', medium: '18px', large: '20px' }
const textSizes = { small: '12px', medium: '14px', large: '16px' }
const iconSizes = { small: '45px', medium: '60px', large: '80px' }
const formSizes = { small: '280px', medium: '320px', large: '360px' }

const getSize = (size, size_custom, options = textSizes) => {
    return options[size] || size_custom + 'px';
}

// When to show form.
const showForm = ref(false);
const showGreetings = ref(true);
onMounted(() => {
    window.addEventListener('scroll', () => {
        const customize_form = document.getElementById('customize_form');
        if (customize_form) {
            showForm.value = customize_form.getBoundingClientRect().top < window.innerHeight - 500;
        }
    });

    // On click [data-name] elements.
    document.addEventListener('click', (e) => {
        if (e.target.dataset.name) {
            // console.log('clicked', e.target.dataset.name);
            const targetElement = document.querySelector(`[name="${e.target.dataset.name}"]`)
            if (targetElement) {
                // Scroll to 100px above the element.
                targetElement.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'nearest' });

                setTimeout(() => {
                    targetElement.focus();
                }, 500)
            }
        }
    })

});

// Greetings handle.
const hideGreetings = () => {
    showGreetings.value = false;

    setTimeout(() => {
        showGreetings.value = true;
    }, 2000)
}


</script>
