<script setup>
import { ref, computed, reactive, onMounted } from 'vue';
import { useWidgets } from '@store/widgets';
import { Group, Tabs, InputLabel, InputGroup, IconUploader, Text, Button, Select, Toggle, NewBadge, Textarea, ColorPicker, Spin } from '@components';
import API from '@util/API'

const state = reactive({})
const $widget = useWidgets();

const errors = computed( () => $widget.errors || {} );

const whatsapp = ref( $widget.currentWidget.config?.whatsapp || {}  )
const icon = ref( $widget.currentWidget.config?.icon || {}  )
const form = ref( $widget.currentWidget.config?.form || {}  )
const cta = ref( $widget.currentWidget.config?.cta || {}  )
const email = ref( $widget.currentWidget.config?.email || {}  )
const cf7 = ref( $widget.currentWidget.config?.cf7 || {} )

const sizes = ref({
    'small': 'Small',
    'medium': 'Medium',
    'large': 'Large',
    'custom': 'Custom',
});

const positions = ref({
    'left': 'Left',
    'right': 'Right',
    'custom': 'Custom',
});

const formychat_admin_vars = window.formychat_admin_vars || {};

const admin_email = formychat_admin_vars.site.admin_email || '';

// Form Mode.
const setFormMode = mode => {

    $widget.setError('cf7_not_active', false)
    $widget.setError('cf7_not_installed', false)

    if ( mode == 'cf7' ) {
        if ( ! formychat_admin_vars.cf7.is_active ) {
            if ( formychat_admin_vars.cf7.is_installed ) {
                return $widget.setError('cf7_not_active')
            }

            return $widget.setError('cf7_not_installed')
        }

        // Set cf7.form_id if not set.
        if ( ! cf7.value.form_id ) {
            cf7.value.form_id = $widget.contents.cf7_forms[0]?.value || ''
        }

        // Load CF7 form.
        loadCF7Form()
    }

    form.value.mode = mode
}

// Activate CF7
const activateCF7 = async ( action = 'install ') => {
    state.processingCF7 = action;

    await API.get('action?action=activate_cf7');

    formychat_admin_vars.cf7.is_active = true
    formychat_admin_vars.cf7.is_installed = true

    setFormMode('cf7') 

    await $widget.fetchContents()

    // Select first form.
    cf7.value.form_id = $widget.contents.cf7_forms[0]?.value || ''

    // Load CF7 form.
    loadCF7Form()

    state.processingCF7 = false;
}

const isLocked = computed( () => ! formychat_admin_vars.is_premium )

const loadCF7Form = () => {
    $widget.currentWidget.cf7_form = ''
    API.get('action?action=load_cf7_form&id=' + cf7.value.form_id).then( response => {
        // Remove form action
        response.data.form = response.data.form.replace(/action="[^"]+"/g, 'action="javascript:;"')
        $widget.currentWidget.cf7_form = response.data.form || ''
    })
}

onMounted(() => {
    if ( form.value.mode == 'cf7' ) {
        loadCF7Form()
    }
})

const YouTubeId = ref('')

</script>
<template>
    <div  class="w-full h-full">
        <div class="flex flex-col gap-10 w-full">

            <!-- Customize Icon  -->
            <Group title="Customize Icon" :gap="false">
                <InputGroup>
                    <InputLabel hints="Select the size of the icon" locked> Custom Icon </InputLabel>
                    <IconUploader :class="{ 'formychat-locked child:pointer-events-none child:opacity-60' : isLocked }" :default="Image('whatsapp.svg')" v-model="icon.image_url" v-model:custom="icon.image_url_custom" />
                </InputGroup>

                <InputGroup>
                    <InputLabel hints="Select the size of the icon"> Icon Size </InputLabel>
                    <Tabs :options="sizes" :locked="['custom']" width="83px" class="bg-gray-50" v-model="icon.size"/>
                </InputGroup>  
                
                <InputGroup :condition="icon.size == 'custom'">
                    <InputLabel hints="Select the size of the icon"> Custom Icon Size </InputLabel>
                    <div class="w-full flex items-center justify-between gap-2">
                        <input class="formychat-slider" type="range" v-model="icon.size_custom" min="20" max="300">
                        <span class="w-16">{{ icon.size_custom }}px</span>
                    </div>
                </InputGroup>

                <InputGroup>
                    <InputLabel hints="Select the position of the icon"> Icon Position </InputLabel>
                    <Tabs :options="positions" :locked="['custom']" width="83px" class="bg-gray-50" v-model="icon.position"/>
                </InputGroup>

                <InputGroup :condition="icon.position == 'custom'">
                    <InputLabel hints="Select the position of the icon"> Custom Icon Position </InputLabel>
                    <div class="flex items-center gap-6 flex-wrap">
                        <div v-for="direction in ['left', 'right', 'top', 'bottom']" :key="direction" class="flex flex-col gap-2">
                            <InputLabel class="capitalize text-gray-500" :for="`icon_position_custom_${direction}`">{{ direction }}</InputLabel>
                            <div class="flex items-center gap-1 w-24"><Text :id="`icon_position_custom_${direction}`" type="number" class="text-sm w-20" placeholder="auto" v-model="icon.position_custom[direction]" /><span v-if="icon.position_custom[direction] !== ''" class="text-gray-400">px</span></div>
                        </div>
                    </div>
                </InputGroup>
            </Group>

            <!-- Divider  -->
            <div class="border-b-2 border-dashed border-gray-200 rounded"></div>

            <!-- Call to action  -->
            <Group title="Call to action" :gap="false">
                <InputGroup>
                    <Toggle v-model="cta.enabled">Show Call to action</Toggle>
                </InputGroup>  

                <InputGroup :condition="cta.enabled">
                    <InputLabel> Call to action text </InputLabel>
                    <Text placeholder="Call to action text" v-model="cta.text"/>
                </InputGroup>  


                <InputGroup :condition="cta.enabled">
                    <InputLabel hints="Select the size of the icon" locked>Call to action style</InputLabel>
                    <div class="flex items-center gap-3" :class="{'opacity-60 formychat-locked child:pointer-events-none' : isLocked}">
                        <ColorPicker v-model="cta.color"> Text Color </ColorPicker>
                        <ColorPicker v-model="cta.background_color"> Background </ColorPicker>
                    </div>
                </InputGroup>

                
                <InputGroup :condition="cta.enabled">
                    <InputLabel hints="Select the size of the icon"> Text Size</InputLabel>
                    <Tabs class="bg-gray-50" :options="sizes" width="85px" v-model="cta.size" :locked="['custom']"/>
                </InputGroup>

                <InputGroup :condition="cta.enabled &&cta.size == 'custom'">
                    <InputLabel hints="Select the size of the icon"> Custom Text Size </InputLabel>
                    <div class="w-full flex items-center justify-between gap-2">
                        <input class="formychat-slider" type="range" min="8" max="72" v-model="cta.size_custom">
                        <span class="w-16">{{ cta.size_custom || 8 }}px</span>
                    </div>
                </InputGroup>
            </Group>

            <!-- Divider  -->
            <div class="border-b-2 border-dashed border-gray-200 rounded"></div>


            <!-- Customize Form  -->
            <Group title="Customize Form" :gap="false" id="customize_form"
            v-if="!(whatsapp.agent_mode === 'agents' && whatsapp.click_agent_to_redirect)"
            >
                <InputGroup>
                    <InputLabel> Choose the source through which you want to send message to WhatsApp </InputLabel>
                    <div class="flex items-center gap-5 child:bg-white child:w-44 child:h-40 child:flex child:items-center child:justify-center child:ring child:rounded child:cursor-pointer child:relative child:transition text-slate-600 text-medium text-sm">
                        <div v-for="(label, mode) in { formychat: 'FormyChat', cf7: 'Contact Form 7' }" @click.prevent="setFormMode( mode )" :class="{ 'ring-indigo-600' : form.mode == mode, 'ring-slate-100 hover:ring-slate-300' : form.mode !== mode }">
                            <img :src="Image(mode + '.png')" class="w-40">
                            <span class="absolute left-0 w-full text-center bottom-3">{{ label }}</span>
                            <transition name="bounce">
                                <svg v-if="form.mode == mode" xmlns="http://www.w3.org/2000/svg" class="w-5 fill-indigo-500 text-white absolute right-2 top-2" viewBox="0 0 16 16">
                                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0m-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                                </svg>
                            </transition>
                        </div>
                    </div>
                </InputGroup>  

                <transition name="slide">
                    <div v-if="errors.cf7_not_active || errors.cf7_not_installed" class="flex items-start gap-5 px-6 py-5 bg-gray-100 border border-gray-200 rounded text-gray-700 w-full break-words">
                        <svg xmlns="http://www.w3.org/2000/svg" class="fill-current w-10" viewBox="0 0 16 16">
                            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16"/>
                            <path d="M7.002 11a1 1 0 1 1 2 0 1 1 0 0 1-2 0M7.1 4.995a.905.905 0 1 1 1.8 0l-.35 3.507a.552.552 0 0 1-1.1 0z"/>
                        </svg>
    
                        <p class="font-medium text-[13px] tracking leading-relaxed">
                            Contact Form 7 is not <strong class="font-bold">activated yet</strong>! Please Active it first to send unlimited messages through any of your CF7 forms
                        </p>
    
                        <div>
                            <Button :class="{ 'opacity-60 pointer-events-none' : state.processingCF7 }" @click="activateCF7( errors.cf7_not_inactivateCFstalled ? 'install' : 'active' )">
                                <span class="flex items-center gap-2" v-if="state.processingCF7">{{errors.cf7_not_installed ? 'Installing' :  'Activating' }} <Spin class="w-4 h-4" /></span>
                                <span v-else>{{errors.cf7_not_installed ? 'Install' : 'Activate'}}</span>
                            </Button>
                        </div>
                    </div>
                </transition>

                <InputGroup :condition="form.mode == 'cf7'">
                    <InputLabel> <span name="cf7_form_id">Select Contact Form 7</span> </InputLabel>
                    <div class="flex items-center gap-4">
                        <Select @select="errors.cf7_form_id = false, loadCF7Form()" placeholder="Select form" name="cf7_form_id" v-model="cf7.form_id" :class="{'ring-red-500' : errors.cf7_form_id}" class="w-40" :cursor="true" :options="$widget.contents.cf7_forms || []"/>
                    </div>
                    <div>
                        You can customize the preset message and email notifications from the selected CF7 form. <a class="text-indigo-500" :href="`${formychat_admin_vars.site.url }/wp-admin/admin.php?page=wpcf7&post=${cf7.form_id}&action=edit#formychat`">Customize now.</a>
                    </div>
                </InputGroup>

                <InputGroup v-if="form.mode == 'formychat'">
                    <div class="flex items-center gap-3">
                        <Toggle locked v-model="email.enabled" hints="Enable this option to receive all lead information to your given email address">Receive leads via email</Toggle>
                        <NewBadge />
                    </div>
                </InputGroup>

                <InputGroup v-if="form.mode == 'formychat'" class="space-y-2" :condition="email.enabled || false ">
                    <label><input type="checkbox" class="bg-transparent" v-model="email.admin_email" :value="admin_email">Use admin email to receive leads</label>
                    <Text v-if="email.admin_email" type="email" :placeholder="admin_email" class="pointer-events-none"/>
                    <Text name="email_address" v-model="email.address" :error="errors.email_address" v-else type="email" placeholder="Enter email address" />
                    <p class="text-sm text-sate-500 py-4 px-6 bg-gray-100 border border-gray-200 rounded"> <strong class="text-semibold">Note:</strong> Please configure any SMTP e.g. <a class="underline" :href="`${formychat_admin_vars.site.url}/wp-admin/plugin-install.php?s=SMTP&tab=search&type=term`" target="_blank">WP Mail SMTP</a> or <a class="underline" :href="`${formychat_admin_vars.site.url}/wp-admin/plugin-install.php?s=SMTP&tab=search&type=term`" target="_blank">FluentSMTP</a> plugin to receive mail. </p>
                </InputGroup>

                <!-- size  -->
                <InputGroup>
                    <InputLabel> Form Size </InputLabel>
                    <Tabs :options="sizes" v-model="form.size" width="85px" class="bg-gray-50"/>
                </InputGroup>

                <InputGroup :condition="form.size == 'custom'">
                    <InputLabel> Custom Form Size </InputLabel>
                    <div class="w-full flex items-center justify-between gap-2">
                        <input class="formychat-slider" type="range" min="100" max="1000" v-model="form.size_custom">
                        <span class="w-16">{{ form.size_custom || 300 }}px</span>
                    </div>
                </InputGroup>

                <!-- Form  -->
                <InputGroup >
                    <InputLabel> Form Title </InputLabel>
                    <Text placeholder="Form Title (Ex: Contact via WhatsApp)" v-model="form.title" name="form_title"/>
                </InputGroup>

                <InputGroup>
                    <InputLabel hints="Leave blank to disable"> Header Text </InputLabel>
                    <div class="w-full">
                        <Textarea placeholder="Header Text (Ex: Please fill up the fields below)" class="resize-y" name="form_subtitle" v-model="form.subtitle"/>
                    </div>
                </InputGroup>

                <InputGroup>
                    <InputLabel hints="Leave blank to disable"> Footer Text </InputLabel>
                    <div class="w-full">
                        <Textarea placeholder="Footer Text (Ex: Powered by FormyChat)" class="resize-none" name="form_footer" v-model="form.footer"/>
                    </div>
                </InputGroup>

                <InputGroup :condition="form.mode == 'formychat'">
                    <InputLabel> Submit Button Text </InputLabel>
                    <Text placeholder="Button Text (Ex: Send on WhatsApp)" name="form_submit" v-model="form.submit"/>
                </InputGroup>

                <InputGroup class="space-y-3" :condition="form.mode == 'formychat'">
                    <div class="flex items-center gap-3">
                        <Toggle v-model="form.show_country_code_field" hints="This will add country code with number to choose." locked> Allow users to select country code</Toggle>
                        <NewBadge />
                    </div>

                    <transition name="slide">
                        <div class="flex items-center gap-2" v-if="form.show_country_code_field">
                            <InputLabel> Default Country Code </InputLabel>
                            <Select v-model="form.country_code" class="w-fit" :options="$widget.contents.countries" placeholder="Select country" />
                        </div>
                    </transition>
                </InputGroup>

                <InputGroup>
                    <InputLabel locked> Form Style </InputLabel>
                    <div class="flex items-center gap-4" :class="{'opacity-60 formychat-locked child:pointer-events-none' : isLocked}">
                        <ColorPicker v-model="form.text_color">Text Color</ColorPicker>
                        <ColorPicker v-model="form.background_color">Background</ColorPicker>
                    </div>
                </InputGroup>

                <InputGroup class="w-full">
                    <InputLabel>Font Family</InputLabel>
                    <Select :options="$widget.contents.fonts" v-model="form.font_family" placeholder="Select font" class="w-56"/>
                </InputGroup>

            </Group>

            <!-- Divider  -->
            <div class="border-b-2 border-dashed border-gray-200 rounded"></div>

            <!-- Form behavior  -->
            <Group title="Form Behavior" :gap="false">
                <InputGroup class="space-y-3">
                    <Toggle v-model="form.open_by_default" hints="Open the form by default when the page is loaded.">Open by default</Toggle>  
                    <Toggle v-model="form.close_on_submit" hints="Close the form after a successful submission.">Close on Submit</Toggle>
                      </InputGroup>
            </Group>

        </div>

        <!-- YouTube Video  -->
        <transition name="fade">
            <div v-if="YouTubeId" class="absolute left-0 top-0 w-full flex h-full z-20 items-center justify-center">
                <div class="absolute top-0 left-0 w-full h-full bg-black opacity-50" @click="YouTubeId = ''"></div>
                <div class="fixed top-1/2 -translate-y-1/2 w-full max-w-xl h-96 z-50">
    
                    <!-- close  -->
                    <div @click="YouTubeId = ''" class="absolute -top-10 -right-10 cursor-pointer w-10 h-10 flex items-center justify-center text-white hover:brightness-110 hover:scale-110 transition">
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" class="h-5 w-5 fill-current">
							<path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12"></path>
						</svg>
                    </div>
    
                    <iframe class="w-full h-full" :src="'https://www.youtube.com/embed/' + YouTubeId + '?autoplay=1'"></iframe>
                </div>
            </div>
        </transition>
    </div>
</template>
