import { createApp } from "vue";
import { createPinia } from "pinia";
import { createRouter, createWebHashHistory } from "vue-router";

import Widgets from "./views/Widgets.vue";
import Leads from "./views/Leads.vue";
import { routes } from './routes/widgets'

(() => {
    const widgetsApp = createApp(Widgets).use(createPinia());
    const leadsApp = createApp(Leads).use(createPinia());

    // Global properties.
    const Image = img => formychat_admin_vars.public + "images/" + img;
    widgetsApp.config.globalProperties.Image = Image;
    leadsApp.config.globalProperties.Image = Image;

    // Router for widgets.
    widgetsApp.use(createRouter({
        history: createWebHashHistory(),
        routes: routes
    }));

    document.addEventListener('DOMContentLoaded', () => {
        widgetsApp.mount("#formychat-widgets")
        leadsApp.mount("#formychat-leads")
    });
})()