<script setup>
import { computed, onMounted } from 'vue';
import { Button } from '@components';
const scf = formychat_admin_vars || {}

import { useUpgrade } from '@store/Upgrade.js';
const $upgrade = useUpgrade();

onMounted( () => {
    document.addEventListener('click', (e) => {
        // Is .formychat-locked clicked or any of its children?
        if (e.target.closest('.formychat-locked')) {
            e.preventDefault();
            $upgrade.show()
        }
    });
})

const isHalloween = computed( () => {
    // If time between 21 october 4pm to 5th nov 4pm, return halloween image
    const now = new Date();
    const start = new Date(now.getFullYear(), 9, 21, 16, 0, 0);
    const end = new Date(now.getFullYear(), 10, 5, 16, 0, 0);
    if (now > start && now < end) {
        return true;
    }

    return false;
})

const bg_image = computed( () => {
    if ( isHalloween.value ) {
        return 'halloween.png';
    }
    return 'upgrade.png';
})

const btn_text = computed( () => {
    if ( isHalloween.value ) {
        return 'Grab Your Treat!';
    }
    return 'GET PREMIUM';
})
</script>

<template>
    <div class="z-50" v-if="!scf.is_premium">
        <transition name="fast-fade">
            <div v-if="$upgrade.isShown" class="flex items-center h-full w-full justify-center z-50">
                <div @click="$upgrade.hide" class="absolute z-40 left-0 top-0 w-full h-full bg-black opacity-20"></div>
                <div class="flex items-center justify-center">
                    <div class="fixed w-full max-w-lg flex flex-col gap-4 h-auto top-1/2 -translate-y-1/2 z-50 shadow-lg drop-shadow-lg">
                        <img :src="Image( bg_image )" class="h-full w-auto mx-auto">
                        <!-- close  -->
                        <svg @click="$upgrade.hide" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-gray-500 absolute hover:scale-125 transition right-4 top-4 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
                        </svg>
        
                        <div class="absolute bottom-4 left-1/2 -translate-x-1/2">
                            <Button class="font-medium px-6 h-12 text-base" a href="https://go.wppool.dev/2rc7" target="_blank">{{ btn_text }}</Button>

						    <div class="text-center mt-4">
                                <a href="https://go.wppool.dev/4jgJ" class="text-indigo-200 cursor-pointer hover:text-white transition text-base" target="_blank">Try a free demo</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
