<script setup>
import { computed, getCurrentInstance } from 'vue';
import Tooltip from './Tooltip.vue';
import ProBadge from './ProBadge.vue';

const props = defineProps({
    hints: {
        type: String,
        default: ''
    },
    for: {
        type: String,
        default: ''
    },
    locked: {
        type: Boolean,
        default: false,
    }
});

const theFor = computed( () => {
    const instance = getCurrentInstance();
    return props.for || instance.parent.props.id || 'scf-element-' + instance.parent.uid;
})

const locked = computed( () => {
    return props.locked && ! formychat_admin_vars.is_premium ? 'formychat-locked child:pointer-events-none opacity-60' : null
})

</script>

<template>
    <div class="flex items-center gap-3 w-fit" v-bind="$attrs">
        <label class="font-normal text-slate-600 tracking-wide text-sm" :for="theFor" :class="locked">
            <slot/>
        </label>
        <Tooltip v-if="props.hints">
            {{ props.hints }}
        </Tooltip>
        <ProBadge v-if="locked" />
    </div>
</template>