<script setup>
import { ref, computed } from 'vue';
import { Button, Pagination, Sort, Select, Tabs, Text } from '@components';
import Modal from './Modal.vue';

import { useModal } from '@store/modal'
import { useLeads } from '@store/leads';

const $modal = useModal();
const $lead = useLeads();

const deleteLeads = async ( id ) => {
    const isDelete = await $modal.prompt({
        title: Array.isArray( id ) ? `Delete selected ${id.length} Leads` : 'Delete Lead',
        message: Array.isArray( id ) ? `Are you sure you want to delete selected ${id.length} leads?` : 'Are you sure you want to delete this lead?',
    })
    
    if ( ! isDelete.ok ) return;

	// console.log('delete leads', id);

    $lead.deleteLeads( id )
}



// Export CSV.
const exportCSV = () => {
	let leads = $lead.leads.map( l => {
		return {
			date: l.created_at.split(' ')[0],
			time: l.created_at.split(' ')[1],
			...l.field,
			...l.meta,
		}
	});

	let csv = Object.keys( leads[0] ).join(',') + '\n';
	leads.forEach( l => {
		csv += Object.values( l ).join(',') + '\n';
	});

	// console.log( csv );

	let blob = new Blob([csv], { type: 'text/csv' });
	let a = document.createElement('a');

	a.href = URL.createObjectURL( blob );
	a.download = 'leads.csv';
	a.click();

	URL.revokeObjectURL( a.href );

}

import Lead from './Lead.vue';
import { Upgrade } from '@components';

const isLockedPage = computed( () =>  {
	if ( formychat_admin_vars.is_premium ) return false;
	
	// after first 20 leads, the page is locked. Consider if per_page is changed.
	return $lead.filters.page > 1 || $lead.filters.per_page > 20;
});
</script>
<template>
	<div class="w-full p-3 sm:p-6 h-full">
		<div class="bg-white p-2 sm:p-6 w-full h-full flex flex-col gap-3 sm:gap-5">
			<!-- top  -->
			<div class="flex items-center justify-between gap-2 h-10 w-full">
				<h3 class="font-semibold text-base whitespace-nowrap">Contact Form Leads </h3>
				<div class="flex items-center :justify-end gap-3 justify-between child:justify-center child:px-3 sm:child:px-6">
					<transition name="bounce">
						<Button class="flex items-center text-base" color="danger" v-if="$lead.selected.length" outline @click="deleteLeads($lead.selected.map( l => l.id))">
							<svg xmlns="http://www.w3.org/2000/svg" class="fill-current w-4" viewBox="0 0 16 16">
								<path d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5m-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5M4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06m6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528M8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5-.5"/>
							  </svg>
							{{$lead.selected.length}}</Button>
					</transition>
					<transition name="bounce">
						<Button v-if="$lead.leads.length && !isLockedPage" @click="exportCSV" outline><svg xmlns="http://www.w3.org/2000/svg" class="fill-current w-4" viewBox="0 0 16 16">
							<path fill-rule="evenodd" d="M14 4.5V14a2 2 0 0 1-2 2h-1v-1h1a1 1 0 0 0 1-1V4.5h-2A1.5 1.5 0 0 1 9.5 3V1H4a1 1 0 0 0-1 1v9H2V2a2 2 0 0 1 2-2h5.5zM3.517 14.841a1.13 1.13 0 0 0 .401.823q.195.162.478.252.284.091.665.091.507 0 .859-.158.354-.158.539-.44.187-.284.187-.656 0-.336-.134-.56a1 1 0 0 0-.375-.357 2 2 0 0 0-.566-.21l-.621-.144a1 1 0 0 1-.404-.176.37.37 0 0 1-.144-.299q0-.234.185-.384.188-.152.512-.152.214 0 .37.068a.6.6 0 0 1 .246.181.56.56 0 0 1 .12.258h.75a1.1 1.1 0 0 0-.2-.566 1.2 1.2 0 0 0-.5-.41 1.8 1.8 0 0 0-.78-.152q-.439 0-.776.15-.337.149-.527.421-.19.273-.19.639 0 .302.122.524.124.223.352.367.228.143.539.213l.618.144q.31.073.463.193a.39.39 0 0 1 .152.326.5.5 0 0 1-.085.29.56.56 0 0 1-.255.193q-.167.07-.413.07-.175 0-.32-.04a.8.8 0 0 1-.248-.115.58.58 0 0 1-.255-.384zM.806 13.693q0-.373.102-.633a.87.87 0 0 1 .302-.399.8.8 0 0 1 .475-.137q.225 0 .398.097a.7.7 0 0 1 .272.26.85.85 0 0 1 .12.381h.765v-.072a1.33 1.33 0 0 0-.466-.964 1.4 1.4 0 0 0-.489-.272 1.8 1.8 0 0 0-.606-.097q-.534 0-.911.223-.375.222-.572.632-.195.41-.196.979v.498q0 .568.193.976.197.407.572.626.375.217.914.217.439 0 .785-.164t.55-.454a1.27 1.27 0 0 0 .226-.674v-.076h-.764a.8.8 0 0 1-.118.363.7.7 0 0 1-.272.25.9.9 0 0 1-.401.087.85.85 0 0 1-.478-.132.83.83 0 0 1-.299-.392 1.7 1.7 0 0 1-.102-.627zm8.239 2.238h-.953l-1.338-3.999h.917l.896 3.138h.038l.888-3.138h.879z"/>
						  </svg> Export</Button>
					</transition>
				</div>
			</div>
			
			<!-- filter  -->
			<div class="flex items-center justify-between flex-col gap-2 sm:flex-row">
				<!-- mode  -->
				<div class="flex items-center gap-2 flex-col sm:flex-row">
					<Tabs :options="$lead.state.modes" width="130px" v-model="$lead.filters.mode" />
					<Select white placeholder="Select Widget" v-if="$lead.filters.mode === 'formychat'" :options="$lead.contents.widgets" v-model="$lead.filters.widget_id"/>
					<Select white placeholder="Select Form" v-else :options="$lead.contents.cf7_forms" v-model="$lead.filters.cf7_id"/>
				
				</div>
				<!-- filters  -->
				<div class="flex items-center gap-3 child:text-[14px]">
					<Button class="w-10 flex items-center justify-center" v-if="$lead.showResetFilter" outline @click="$lead.resetFilter()">
						<svg xmlns="http://www.w3.org/2000/svg" class="fill-current w-5" viewBox="0 0 16 16">
						<path fill-rule="evenodd" d="M8 3a5 5 0 1 1-4.546 2.914.5.5 0 0 0-.908-.417A6 6 0 1 0 8 2z"/>
						<path d="M8 4.466V.534a.25.25 0 0 0-.41-.192L5.23 2.308a.25.25 0 0 0 0 .384l2.36 1.966A.25.25 0 0 0 8 4.466"/>
					  </svg></Button>
					<input type="date" v-model="$lead.filters.after"
						class="h-10  border border-slate-200 rounded cursor-pointer transition-all px-3.5" :class="{ 'w-12 focus:w-[135px]' : !$lead.filters.after, 'w-[135px]' : $lead.filters.after }" placeholder="From">
					<span class="text-gray-500">to</span>
					<input type="date" v-model="$lead.filters.before"
					class="h-10  border border-slate-200 rounded cursor-pointer transition-all px-3.5" :class="{ 'w-12 focus:w-[135px]' : !$lead.filters.before, 'w-[135px]' : $lead.filters.before }" >
					
					<Text type="search" white v-model="$lead.filters.search" placeholder="🔍  Search" class="transition-all" :class="{ 'pr-0 w-12 focus:w-44' : !$lead.filters.search, 'w-44' : $lead.filters.search }"/>
				</div>
			</div>

			<!-- loading  -->
			<div v-if="$lead.state.isLoading" class="">
				<div class="flex items-center justify-center gap-4 my-6">
					<div class="w-5 h-5 border-2 border-y-transparent border-indigo-500 animate-spin rounded-full">
					</div>
					<span class="text-base">Please wait while we load your leads.</span>
				</div>
			</div>

			<!-- table  -->
			<div v-else class="flex flex-col gap-6 h-full"> 
				<div :class="{'pointer-events-none animate-pulse cursor-wait' : $lead.state.isReloading}" class="overflow-x-scroll h-full sm:overflow-visible">
					<!-- table head  -->
					<div
						class="flex sm:sticky sm:top-7 items-center child:py-2 bg-indigo-100 child:px-5 child:font-semibold w-[800px] sm:w-full h-full">
						<div class="w-16"><input type="checkbox" class="bg-transparent" v-model="$lead.state.allSelected" @change="$lead.toggleAll"
							:class="{'pointer-events-none opacity-30' : isLockedPage}" /></div>
						<div class="w-1/6 capitalize" v-if="$lead.columns[0]">{{ $lead.columns[0] }}</div>
						<div class="w-1/6 capitalize" v-if="$lead.columns[1]">{{ $lead.columns[1] }}</div>
						<div class="w-1/6 capitalize" v-if="$lead.columns[2]">{{ $lead.columns[2] }}</div>
						<div class="w-2/6 capitalize" v-if="$lead.columns[3]">{{ $lead.columns[3] }}</div>
						<div class="w-2/6 capitalize" v-if="$lead.filters.mode == 'formychat'">Widget</div>
						<div class="w-1/6" @click="$lead.setSort('created_at')">
							<div class="flex items-center gap-2">Date <Sort :asc="$lead.filters.order_by == 'created_at' && $lead.filters.order == 'asc'"/></div>
						</div>
						<div class="w-[150px]">Action</div>
					</div>
					<!-- table head  -->
					<Lead v-if="$lead.leads.length" v-for="(lead, index) in $lead.leads" :key="lead.id" :lead="lead" @delete="deleteLeads" @select="$lead.toggleSelector" class="w-[800px] sm:w-full" :locked="isLockedPage"/>
					<div v-else class="text-center p-5 sm:p-10 text-slate-500 text-base">
						No leads found.
					</div>
				</div>

				<!-- pagination  -->
				<Pagination v-if="$lead.leads.length" label="lead" :class="{'pointer-events-none animate-pulse cursor-wait' : $lead.state.isReloading}" v-model:page="$lead.filters.page" v-model:perPage="$lead.filters.per_page"
					:total="$lead.total" />
			</div>


			<Upgrade />
		</div>

		<Modal />
	</div>
</template>
<style scoped>
#wpcontent {
	padding-left: 0 !important;
}
</style>