<script setup>
import { ref, computed, onMounted } from 'vue';
import { useWidgets } from '@store/widgets';
import { Group, Select, Text, Button, IconUploader, Tooltip, Toggle, InputGroup, InputLabel, ProBadge } from '@components';

const $widget = useWidgets();
const whatsapp = computed( () => $widget.currentWidget?.config?.whatsapp || {} )
const isLocked = !window.formychat_admin_vars.is_premium || false

// Use modal store.
import { useModal } from '@store/modal'
const $modal = useModal();

const isPhoneValid = ( number ) => {
    return number?.match(/^\d{7,15}$/);
}

// Preset message.
const presetPreview = ref(false)

const variables = {
    name: 'John Doe',
    email: 'johndoe@example.com',
    phone: '+1234567890',
    message: 'Hello, this is a dummy message',
    site_name: formychat_admin_vars.site.name || 'Example Site',
    site_url: formychat_admin_vars.site.url || 'https://example.com',
    user_id: '123456',
    widget_name: $widget.currentWidget.name || 'Widget Name',
}

const formatRules = {
    bold: {
        pattern: /\*(.*?)\*/,
        code: '*$1*',
        html: '<strong>$1</strong>',
        label: 'b',
        description: 'Bold',
    },
    italic: {
        pattern: /_(.*?)_/,
        code: '_$1_',
        html: '<em>$1</em>',
        label: 'i',
        description: 'Italic',
    },
    del: {
        pattern: /~(.*?)~/,
        code: '~$1~',
        html: '<del>$1</del>',
        label: 's',
        description: 'Strikethrough',
    },
    code : {
        pattern: /`(.*?)`/,
        code: '`$1`',
        html: '<code>$1</code>',
        label: '`',
        description: 'Code',
    },
    monospace: {
        pattern: /```(.*?)```/,
        code: '```$1```',
        html: '<pre>$1</pre>',
        label: '```',
        description: 'Monospace',
    },
    line: {
        pattern: /\n/,
        code: "$1\n",
        html: '<br />',
        label: '↵',
        description: 'New Line',
    }
}

const previewPreset = computed(() => {
    let message = whatsapp.value.message_template || '';

    // Stripe HTML tags
    message = message.replace(/<[^>]*>/g, '');

    // Apply formatting rules
    Object.values(formatRules).forEach(rule => {
        // Replace all occurrences of the pattern
        message = message.replace(new RegExp(rule.pattern, 'gm'), rule.html);
    });

    // Replace variables
    Object.keys(variables).forEach(variable => {
        message = message.replace(new RegExp(`{${variable}}`, 'gm'), variables[variable]);
    });

    return message;
});

const template = ref(null)

const applyRule = (rule) => {
    const input = template.value;
    const start = input.selectionStart;
    const end = input.selectionEnd;
    const text = input.value;
    const selected = text.substring(start, end);

    // if already rule applied, remove it

    let updatedText = ''
    if ( selected.match(rule.pattern) ) {
        updatedText = text.substring(0, start) + selected.replace(rule.pattern, '$1') + text.substring(end);

        // Update the message template
        input.value = updatedText;

        input.focus();

        // Update selection.
        input.setSelectionRange(start, end - (selected.length - selected.replace(rule.pattern, '$1').length));

        whatsapp.value.message_template = updatedText;
    } else {
        let markedUp = rule.code.replace('$1', selected);
        updatedText = text.substring(0, start) + markedUp + text.substring(end);

        // Update the message template
        input.value = updatedText;

        // Focus the input and set cursor position
        input.focus();

        // Move cursor to end of replaced text or new line
        const newCursorPosition = start + (rule.code === '$1\n' ? 1 : markedUp.length);
        input.setSelectionRange(start + (rule.code === '$1\n' ? 1 : 0), newCursorPosition);

        whatsapp.value.message_template = updatedText;
    }
};

const insert = (variable) => {
    const input = template.value;
    const start = input.selectionStart;
    const end = input.selectionEnd;
    const text = input.value;

    const newText = text.substring(0, start) + `{${variable}}` + text.substring(end);
    
    // Update the message template
    input.value = newText;
    whatsapp.value.message_template = newText;

    // Focus the input and set cursor position
    input.focus();

    // Move cursor to location of inserted variable
    const newCursorPosition = start + variable.length + 2;

    // Select the text
    input.setSelectionRange(newCursorPosition, newCursorPosition);
};

const resetWhatsAppError = () => {
    $widget.errors.whatsapp_number = false;
}



const keyboardEvents = (e) => {

    if (e.key === 'Enter') {
        e.preventDefault();
        applyRule(formatRules.line);
    }

    // Ctrl + B
    if ((e.ctrlKey && e.key === 'b') || (e.metaKey && e.key === 'b')) {
        e.preventDefault();
        applyRule(formatRules.bold);
    }

    // Ctrl + I
    if ((e.ctrlKey && e.key === 'i') || (e.metaKey && e.key === 'i')) {
        e.preventDefault();
        applyRule(formatRules.italic);
    }

    // Ctrl + S
    if ((e.ctrlKey && e.key === 's') || (e.metaKey && e.key === 's')) {
        e.preventDefault();
        applyRule(formatRules.del);
    }
}


const showInserts = ref(false);
const insert_vars = ref(null);
// Anywhere clicked but the insert_vars
window.addEventListener('click', (e) => {
    if (insert_vars.value && !insert_vars.value.contains(e.target)) {
        showInserts.value = false;
    }
})

// Agent 
const addAgent = () => {
    whatsapp.value.agents = whatsapp.value.agents || [];
    whatsapp.value.agents.push({ title: '', subtitle: '', number: '', country_code: '44' });
    $widget.errors.whatsapp_agents = false;
}

const deleteAgent = async (index) => {
    // Use modal to confirm deletion
    const confirmDelete = await $modal.prompt({
        title: 'Delete Agent',
        message: 'Are you sure you want to delete this agent?',
    });

    if ( confirmDelete.ok ) {
        whatsapp.value.agents.splice(index, 1);
    } 
}

const moveAgent = (index, direction) => {
    const agents = whatsapp.value.agents;
    const newIndex = direction === 'up' ? index - 1 : index + 1;

    if (newIndex < 0 || newIndex >= agents.length) {
        return;
    }

    const agent = agents[index];
    agents.splice(index, 1);
    agents.splice(newIndex, 0, agent);
}

// on onMounted.
onMounted(() => {
    whatsapp.value.agent_mode = whatsapp.value.agent_mode || 'single';
})

const PUBLIC = formychat_admin_vars.site
</script>
<template>
    <div class="w-full h-full flex flex-col gap-8">

        <!-- Name  -->
        <InputGroup>
            <InputLabel>Widget name</InputLabel>
            <Text v-model="$widget.currentWidget.name" placeholder="Widget name"/>
        </InputGroup>

        <!-- Agents  -->
        <InputGroup>
            <InputLabel>Choose how you prefer to display your widget</InputLabel>
            <div class="flex items-center gap-5">
                <div v-for="(agentLabel, agent) in { single: 'Single', agents: 'Multi-agents' }" :key="agent" 
                    :class="{
                        'ring-1 ring-slate-200 hover:bg-white bg-gray-50' : whatsapp.agent_mode !== agent,
                        'ring-[2px] ring-indigo-400 ' : whatsapp.agent_mode == agent,
                        'formychat-locked' : isLocked && agent !== 'single'
                    }"
                    @click.prevent="whatsapp.agent_mode = isLocked ? 'single' : agent"
                    class="group relative w-40 h-40 ring-inset rounded cursor-pointer transition flex items-center justify-center flex-col gap-2">
                    <svg xmlns="http://www.w3.org/2000/svg" class="fill-current w-12" viewBox="0 0 16 16">
                        <path v-if="agent === 'single'" d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6"/>
                        <path v-else d="M7 14s-1 0-1-1 1-4 5-4 5 3 5 4-1 1-1 1zm4-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6m-5.784 6A2.24 2.24 0 0 1 5 13c0-1.355.68-2.75 1.936-3.72A6.3 6.3 0 0 0 5 9c-4 0-5 3-5 4s1 1 1 1zM4.5 8a2.5 2.5 0 1 0 0-5 2.5 2.5 0 0 0 0 5"/>
                    </svg>

                    <transition name="bounce">
                        <svg v-if="whatsapp.agent_mode == agent" xmlns="http://www.w3.org/2000/svg" class="fill-indigo-500 w-4 absolute right-2 top-2" viewBox="0 0 16 16">
                            <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0m-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                        </svg>
                    </transition>

                    <!-- Locked. -->
                    <div v-if="isLocked && agent !== 'single'" class="group-hover:opacity-100 transition duration-100 opacity-0 absolute inset-0 bg-white bg-opacity-50 rounded flex items-center justify-center">
                        <ProBadge />
                    </div>

                    <span class="font-medium">{{ agentLabel }}</span>
                </div>
            </div>
        </InputGroup>

        <!-- Single WhatsApp Number  -->
        <InputGroup v-if="whatsapp.agent_mode === 'single'">
            <InputLabel>WhatsApp Number</InputLabel>
            <div class="flex items-center gap-3">
                <Select :options="$widget.contents.countries" v-model="whatsapp.country_code" :cursor="false" class="w-24 justify-center text-center" :placeholder="'Select'" />
                <div class="w-full relative">
                    <Text name="whatsapp_number" v-model="whatsapp.number" @keydown="resetWhatsAppError" type="text" placeholder="WhatsApp number"
                        :error="!isPhoneValid( whatsapp.number ) || $widget.errors.whatsapp_number"/>


                    <transition name="bounce">
                        <div v-if="!isPhoneValid( whatsapp.number ) || $widget.errors.whatsapp_number"
                        class="absolute w-4 text-red-500 right-3 top-3 flex items-center gap-2 transition" :class="{ 'bounce-enter-active' : $widget.errors.whatsapp_number  }">
                        <svg xmlns="http://www.w3.org/2000/svg" class="fill-current" viewBox="0 0 16 16">
                            <path
                                d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5m.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2" />
                        </svg>
                    </div>
                    </transition>
                </div>
            </div>
        </InputGroup>

        <!-- When Agent Mode is ON  -->
        <div class="flex flex-col gap-4" v-if="whatsapp.agent_mode === 'agents'">
            <!-- Agents  -->
            <div v-if="whatsapp.agents && whatsapp.agents.length" v-for="(agent, index) in whatsapp.agents" :key="index" class="flex flex-col gap-4">
                <div class="bg-gray-50 p-3 rounded border border-gray-100 flex flex-col gap-4 relative">

                    <!-- delete  -->
                    <div class="absolute z-10 right-3 top-3 child:cursor-pointer flex items-center gap-3">
                       
                        <Tooltip v-if="index > 0">
                            <svg xmlns="http://www.w3.org/2000/svg" @click.prevent="moveAgent(index, 'up')" class="fill-current rotate-180 w-3 opacity-60 hover:opacity-100 transition" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708"/>
                            </svg>
                            <template #content>
                                Move up
                            </template>
                        </Tooltip>

                       <Tooltip v-if="index < whatsapp.agents.length - 1">
                            <svg xmlns="http://www.w3.org/2000/svg" @click.prevent="moveAgent(index, 'down')" class="fill-current w-3 opacity-60 hover:opacity-100 transition" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708"/>
                            </svg>
                            <template #content>
                                Move down
                            </template>
                       </Tooltip>
                          
                        <svg @click="deleteAgent(index)" xmlns="http://www.w3.org/2000/svg" class="fill-current w-4 hover:opacity-100 transition hover:text-red-500" viewBox="0 0 16 16">
                            <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5M8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5m3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0"/>
                        </svg>
                    </div>

                    <!-- Profile picture. -->
                    <InputGroup>
                        <IconUploader v-model="agent.avatar" v-model:custom="agent.custom_avatar" :default="Image('whatsapp-circle.svg')" />
                    </InputGroup>
                    
                    <!-- Name  -->
                    <InputGroup>
                        <InputLabel> Agent name </InputLabel>
                        <Text placeholder="Example: John Doe" v-model="agent.name"/>
                    </InputGroup>

                    <!-- Subtitle  -->
                    <InputGroup>
                        <InputLabel> Subtitle / Designation </InputLabel>
                        <Text placeholder="Example: Technical Support Executive" v-model="agent.subtitle"/>
                    </InputGroup>

                    <!-- WhatsApp Number  -->
                    <InputGroup>
                        <InputLabel> Agent WhatsApp number </InputLabel>
                        <div class="flex items-center gap-3">
                            <Select :options="$widget.contents.countries" v-model="agent.country_code" :cursor="false" class="w-24 justify-center text-center" :placeholder="'Select'" />
                            <div class="w-full relative">
                                <Text v-model="agent.number" type="text" placeholder="WhatsApp number"/>
            
                                <transition name="bounce">
                                    <div v-if="!isPhoneValid( agent.number ) || $widget.errors.whatsapp_agent_[index]"
                                        :name="'whatsapp_agent_' + index"
                                        class="absolute w-4 text-red-500 right-3 top-3 flex items-center gap-2 transition" :class="{ 'bounce-enter-active' : $widget.errors.whatsapp_number  }">
                                        <svg xmlns="http://www.w3.org/2000/svg" class="fill-current" viewBox="0 0 16 16">
                                            <path
                                                d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5m.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2" />
                                        </svg>
                                    </div>
                                </transition>
                            </div>
                        </div>
                    </InputGroup>
                </div>
            </div>

            <!-- No agent found  -->
            <!-- <div v-else class="bg-indigo-50 border border-indigo-200 px-6 py-4 rounded text-indigo-500">Create your first agent</div> -->
        
            <!-- Add agent  -->

            <!-- When no agents  -->
            <transition name="fade">
                <div v-if="$widget.errors.whatsapp_agents" class="text-red-500 text-sm py-4 flex items-center justify-between px-6 rounded bg-red-50 border border-red-200">
                    Please add at least one agent
                    <span class="cursor-pointer px-3 py-1 bg-red-300 rounded" @click="$widget.errors.whatsapp_agents = false">x</span>
                </div>
            </transition>

           <div> <Button outline class="px-2 h-8" @click="addAgent"><svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" fill="currentColor" class="bi bi-plus-lg" viewBox="0 0 16 16">
            <path fill-rule="evenodd" d="M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2"/>
          </svg> Add new agent</Button> </div>
        </div>

        <!-- Redirect to whatsapp on clicking agent  -->
        <div class="flex items-start gap-8 flex-col">
            <h3 class="font-semibold">Behaviors</h3>

            <Toggle v-model="whatsapp.new_tab" hints="Open the WhatsApp link in a new tab on Desktop. <br/><b>Note:</b> In Safari, WhatsApp will be opened in current tab.">Open WhatsApp in a new tab</Toggle>
           
            <Toggle v-model="whatsapp.web_version" locked hints="By enabling this feature, WhatsApp will be automatically opened on your web
            browser when accessed from a laptop/PC.">Navigate to WhatsApp Web from desktop</Toggle>

            <div v-if="whatsapp.agent_mode === 'agents'" class="flex flex-col gap-3">
                <InputLabel>On click Agent</InputLabel>
                <div class="flex flex-col gap-2.5 child:cursor-pointer">
                    <label class="flex items-center gap-4 group" 
                    v-for="(label, action) in { show_form: 'Load The Selected Form', redirect : 'Redirected to WhatsApp directly' }"
                    @click.prevent="(isLocked && action == 'redirect') ? null : whatsapp.on_click_agent = action"
                    :class="{ 'text-indigo-500' : whatsapp.on_click_agent == action, }"
                    > 
                        <div class="w-5 h-5 bg-white border-[5px] rounded-full  transition" :class="{ 
                            'border-indigo-500' : whatsapp.on_click_agent == action, 
                            'border-gray-100' : whatsapp.on_click_agent !== action,
                            'group-hover:border-indigo-500' : whatsapp.on_click_agent !== action && !isLocked,
                            'formychat-locked' : isLocked && action === 'redirect'
                        }"><span></span></div> <span :class="{
                            'opacity-60 formychat-locked' : isLocked && action === 'redirect'}">{{ label }}</span>
                        <ProBadge v-if="action === 'redirect'" />
                    </label>
                </div>
                
            </div>
        </div>              
        
        <!-- Message template  -->
        <InputGroup v-if="!whatsapp.click_agent_to_redirect || whatsapp.agent_mode !== 'agents'">
            <div class="flex items-center gap-3">
                <InputLabel locked hints="Preset Message is a convenient way of sending quick messages. You can also customize your preset
                    message through this section">Message template</InputLabel>
            </div>

            <div class="w-full relative flex flex-col gap-4" :class="{'formychat-locked opacity-60 child:pointer-events-none' : isLocked }">
        
                <div class="absolute z-10 right-3 top-3 cursor-pointer hover:opacity-100 transition" :class="{'opacity-50' : !presetPreview}" @click="presetPreview = !presetPreview">
                    <Tooltip>
                        <svg xmlns="http://www.w3.org/2000/svg" class="fill-current w-5" viewBox="0 0 16 16">
                            <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0"/>
  <path d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8m8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7"/>
                            <path v-if="!presetPreview" d="M5.525 7.646a2.5 2.5 0 0 0 2.829 2.829zm4.95.708-2.829-2.83a2.5 2.5 0 0 1 2.829 2.829zm3.171 6-12-12 .708-.708 12 12z"/>
                        </svg>
                        <template #content>
                            <div class="whitespace-nowrap">Toggle Preview of Preset Message</div>
                        </template>
                    </Tooltip>
                </div>

                <!-- formatter  -->
                 <div v-if="!presetPreview" class="ring-1 ring-gray-100 rounded">
                    <div class="flex items-center gap-2 border-b border-gray-100 py-2 px-2 flex-wrap">
                        <span class="flex items-center justify-center cursor-pointer h-8 w-10 rounded border border-gray-200 hover:bg-gray-100 transition duration-75 bg-gray-50 active:scale-90 text-gray-700 shadow-sm" v-for="(rule, ruleId) in formatRules" v-html="rule.html.replace( rule.html, rule.label )" :title="rule.description || ''" @click.prevent="applyRule( rule )"></span>
                        <div ref="insert_vars" class="relative h-8 w-16 border border-gray-200 hover:bg-gray-100 transition duration-75 bg-gray-50 cursor-pointer">
                            <span @click="showInserts = !showInserts" class="flex items-center justify-center w-full h-full">Insert</span>

                            <transition name="slide">
                                <div v-if="showInserts" >
                                <div class="absolute translate-y-px top-full left-0 w-44 max-h-56 overflow-scroll bg-white shadow-lg rounded flex-col flex">
                                    <span class="px-4 py-2 text-sm border-b border-gray-50 last:border-0 cursor-pointer hover:bg-gray-50 transition" v-for="v in Object.keys(variables)" @click="insert( v ), showInserts = false">{{ '{' + v + '}' }}</span>
                                </div></div>
                            </transition>
                        </div>
                     </div>
                     
                    <textarea ref="template" @keydown="keyboardEvents" v-html="whatsapp.message_template"
                        @input="whatsapp.message_template = $event.target.value"
                        placeholder="Type your message here"
                        class="w-full text-[14px] h-28 outline-none rounded focus:border-0 leading-normal tracking-wide ring-[2px] ring-transparent focus:ring-indigo-500 border-0 px-4 py-2 transition focus:bg-white bg-gray-50"></textarea>
                 </div>
                <div v-else class="border-0 ring-1 translate-y-px ring-gray-100 rounded">
                    <div class="flex items-center gap-2 border-b border-gray-100 h-12 px-4 font-medium">
                        Preview
                     </div> 

                <div 
                class="h-28 text-[14px] bg-gray-50 px-4 py-2 w-full leading-normal tracking-wide" v-html="previewPreset"> </div>
                 </div>
                
            </div>
        </InputGroup>
    </div>
</template>