<script setup>
import { ref, computed, onMounted } from 'vue';
import { useWidgets } from '@store/widgets';
import { Group, NewBadge, InputLabel, InputGroup, IconUploader, Text, Button, Select, Toggle, Tooltip, Textarea, ColorPicker, ProBadge, MultiSelect } from '@components';

const $widget = useWidgets();
const isLocked = computed ( () => !formychat_admin_vars.is_premium )

const icon = computed( () => $widget.currentWidget.config?.icon || {});
const target = computed( () => $widget.currentWidget.config?.target || {})

const pages = computed( () => $widget.contents.pages ? $widget.contents.pages.map( p => ({ ...p, label: (p.label || 'Untitled Page') }) ) : [] )
import { DotLottieVue } from '@lottiefiles/dotlottie-vue'
const animationPath = formychat_admin_vars.public + '/js/animation.json';


const routeToEdit = () => {
    $widget.states.showSuccess = false
    $widget.currentWidget.isNew = false
    $widget.editWidget( $widget.currentWidget )
}
</script>
<template>
    <div class="w-full h-full space-y-8">
         <Group title="Triggers">
            <InputGroup class="space-y-2">
                <Toggle v-model="$widget.currentWidget.is_active">Activate FormyChat</Toggle>
                <Toggle v-model="icon.has_delay">Time delay</Toggle>
                <div class="transition" :class="{ 'opacity-60 pointer-events-none' : !icon.has_delay}">
                    Show form after <Text class="w-16 inline mx-2" v-model="icon.delay" type="number" min="0" max="9999" placeholder="3" /> seconds when page is loaded
                </div>
            </InputGroup>
         </Group>

         <Group>
            <template #title>
                <div class="flex items-center gap-3">
                    Targets  <ProBadge /> <NewBadge />
                </div>
            </template>
            <div class="flex items-start gap-5"  :class="{'opacity-70 formychat-locked child:pointer-events-none' : isLocked}">
                <InputLabel class="w-40">Exclude pages</InputLabel>
                <InputGroup class="space-y-2">
                    <MultiSelect :options="pages" v-model="target.exclude_pages" :class="{' opacity-60 pointer-events-none' : target.exclude_all_pages}"/>
                    <Toggle v-model="target.exclude_all_pages">Exclude all pages excepts:</Toggle>
                    <MultiSelect :options="pages" v-model="target.exclude_all_pages_except" :class="{' opacity-50 pointer-events-none' : !target.exclude_all_pages}" />
                </InputGroup>
            </div>
         </Group>

         <transition name="form_slide">
            <div v-if="$widget.states.showSuccess" class="absolute -translate-y-8 sm:translate-y-0 w-full h-full left-0 top-0 bg-white z-20">

                <!-- close  -->
                <div class="absolute -top-8 right-0 cursor-pointer w-12 h-12 flex items-center justify-center text-gray-300 hover:text-gray-700 transition">
                    <svg @click.prevent="routeToEdit" xmlns="http://www.w3.org/2000/svg" class="fill-current w-6" viewBox="0 0 16 16">
                        <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708"/>
                      </svg>
                </div>

                <div class="flex flex-col gap-4 justify-center items-center h-96">
                    <DotLottieVue class="w-72" autoplay loop :src="animationPath" />
                    <h2 class="text-2xl text-gray-700">{{ $widget.currentWidget.isNew ? 'Setup Finished!' : 'Widget Updated!'}}</h2>
                    <div class="text-lg text-gray-500" v-if="$widget.currentWidget.is_active">Now you can use <a href="https://go.wppool.dev/2rc7" target="_blank" class="text-indigo-500 hover:text-indigo-400 transition">FormyChat</a></div>
        
                    <div class="flex items-center gap-3 justify-center my-6 font-medium">
                        <Button outline @click="routeToEdit" class="text-lg h-12 px-8">Done</Button>
                        <Button v-if="isLocked" a href="https://go.wppool.dev/2rc7" target="_blank" class="text-lg h-12 px-8" color="danger" outline @click="$widget.states.showSuccess = false">Upgrade all features</Button>
                    </div>
                </div>
            </div>
         </transition>
    </div>
</template>
